@import url(https://fonts.googleapis.com/css2?family=Epilogue:ital,wght@0,200;0,300;0,400;0,500;1,200;1,400&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Epilogue", sans-serif;
}

:root {
  --default-color: rgb(224, 70, 129);
}

nav {
  margin-top: 20px;
  background: white;
  border-bottom: 1px solid white;
  padding: 10px;
  font-size: 16px;
  text-transform: uppercase;
  display: flex;
  letter-spacing: 3px;
  justify-content: center;
  /* position: relative; */
}

ul > a {
  text-decoration: none;
  list-style: none;
  color: #333;
}

ul > a:hover {
  text-decoration: none;
}

li {
  display: inline;
  margin-left: 50px;
  text-decoration: none;
  font-weight: bold;
}

.menu-bar {
  position: absolute;
  right: 24px;
  top: 24px;
  outline: none;
  border: none;
  background: transparent;
  font-size: 2rem;
  z-index: 999999;
}
.menu-bar:focus {
  outline: none;
}

.overlay-navbar {
  position: fixed;
  background: rgb(0, 0, 0, 0.6);
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin-top: 0;
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
}

.overlay-nav-links {
  display: block;
}

.overlay-nav-link {
  color: white;
  display: block;
  text-align: center;
  font-size: 1.75rem;
  padding-top: 10px;
}

.card-component-wrapper {
  background-color: #f8f9fa;
  transition: all 0.2s;
  margin: 0.75rem 0;
}

.card-component-wrapper h1,
.card-title {
  font-size: 3rem;
  text-align: center;
}

.card-component-wrapper:hover {
  /* box-shadow: 0 0 20px 2px rgb(209, 209, 209); */
  background-color: #4849fa;
  color: white;
}

.card-title {
  font-size: large;
  font-weight: 600;
}

.event-component-wrapper {
  padding-top: 10rem;
  padding-bottom: 5rem;
}

.event-component-wrapper .event-image-wrapper {
  /* border: 2px solid black; */
  padding: 5px;
}

.styled-text-pink {
  color: var(--default-color);
}

div.event-wrapper .event-title {
  font-size: 2rem;
  font-weight: 600;
  line-height: 1;
}

div.event-wrapper .event-description-text {
  font-size: 1rem;
  color: rgb(39, 40, 43);
}

.event-component-wrapper .event-image-wrapper .event-image {
  width: 100%;
  border-radius: 2px 4rem;
}

.event-component-header {
  padding: 5px;
  font-size: 6rem;
  font-weight: 800;
  color: transparent;
  text-transform: uppercase;
  /* mask: 1px solid blue; */
  line-height: 1;
}

.text-masked {
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: rgb(204, 203, 203);
}

.learn-more-button {
  padding: 1rem 4rem;
  border: 2px solid var(--default-color);
  color: rgb(252, 252, 252);
  background-color: var(--default-color);
  border-radius: 10rem;
  transition: ease-in-out 0.2s;
  box-shadow: 0px 11px 22px #a7a6a6;
}

.learn-more-button:hover {
  background-color: transparent;
  color: var(--default-color);
}

.hero-banner-wrapper {
  min-height: 50vh;
  background: linear-gradient(-45deg, var(--default-color), #ffffff);
  background-repeat: no-repeat;
  background-position: right;
  background-size: contain;
  padding: 0 4rem;
  position: relative;
  z-index: -1;
}

.banner-heading {
  font-size: 64px;
  font-weight: 700;
  line-height: 1.5;
  color: var(--default-color);
}

.banner-description {
  font-size: 24px;
  font-weight: 500;
}
.banner-description span {
  display: block;
  background: var(--default-color);
  padding: 2px 10px;
  height: 20%;
  width: 20%;
}

@media screen and (max-width: 756px) {
  .hero-banner-wrapper {
    background-size: 50%, 100%;
  }
  .banner-heading {
    font-size: 48px;
  }
}

@media screen and (max-width: 476px) {
  .hero-banner-wrapper {
    background-size: 50%, 100%;
  }
}

.main-text {
  font-size: 2.5rem;
}

@media screen and (max-width: 992px) {
  .main-text {
    font-size: 1.75rem;
  }
}
@media screen and (max-width: 768px) {
  .main-text {
    font-size: 2.5rem;
  }
}

.learn-more-button.button-purple {
  background-color: rgb(49, 0, 184);
  border: 2px solid rgb(49, 0, 184);
}

.learn-more-button.button-purple:hover {
  background: transparent;
  color: rgb(49, 0, 184);
}

.schedule-component-header {
  color: transparent;
  font-size: 6rem;
  font-weight: 800;
}

.bar {
  padding: 2px;
  margin-top: 1rem;
  background-color: var(--default-color);
}

.timeline-header {
  display: block;
  background: var(--default-color);
  /* background-color: aqua; */
  padding: 10px 0px;
  margin: 0.5rem 0rem;
  border-radius: 10px;
  transition: 0.1s;
}

.timeline-header::before {
  content: "";
  height: 0;
  width: 0;
  z-index: -1;
  border-radius: 2px;
  padding: 10px;
  position: absolute;
  bottom: -10px;
  transform: rotate(45deg);
  background-color: var(--default-color);
  /* background-color: #f08ae5; */
}
.timeline-header:hover::before,
.timeline-header:hover {
  background: var(--default-color);
}

.event-timestamp {
  color: white;
}

.timeline-header:hover .event-timestamp {
  color: white;
}

.rounded {
  border-radius: 10px;
}

.feature-image {
  width: 40%;
  float: right;
  margin-right: 1rem;
}
@media screen and (max-width: 960px) {
  .feature-image {
    width: 80%;
  }
}

@media screen and (max-width: 768px) {
  .feature-image {
    width: 100%;
  }
}

.timeout-outer-wrapper {
  /* background-color: rgb(79, 118, 151); */
  /* padding: 1rem; */
  padding: 0;
}

.timeout-wrapper {
  padding:35px;
  background-color: rgb(255, 255, 255);
  margin-left: 10vw;
  margin-top: -2rem;
  z-index: 99;
  /* padding-left: 4rem; */
  border-radius: 10rem 0 0 10rem;
  box-shadow: 0 0 30px rgb(209, 209, 209);
  justify-content: space-evenly;
}

.timeout-content h1 {
  font-size: 2.5rem;
}

.timeout-date-time-wrapper {
  color: rgb(89, 89, 89);
}

.date-time {
  color: var(--default-color);
  font-size: 2.5rem;
}

@media screen and (max-width: 960px) {
  span.date-time {
    font-size: 2rem;
  }
}

@media screen and (max-width: 760px) {
  span.date-time {
    font-size: 1.75rem;
  }
}


@media screen and (max-width: 600px) {

  .timeout-wrapper{
    height:20vh;
    padding:25px;
  }
 
  h1.event-title{

    font-size:20px;
  }

  span.date-time {
    display: inline;
    font-size: 1rem;
  }
}

.partners{
    -webkit-filter: contrast(0.9);
            filter: contrast(0.9);
    border-top:1px solid white;
    color:#ccc;
    border-bottom:2px solid white;
}

@media only screen and (max-width:600px){
    .partners > .row >  .col-lg-4,.col-lg-2{
        
        margin-top:40px;
    }
}

.partner-title{

    text-transform: uppercase;
}

.partner-image{

    height:100px;
    width:100px;
    object-fit:contain;
    margin-left:30px;
}
*{

  margin:0;
  padding:0;
  box-sizing: border-box;
}

.breadcrumb {
  display: flex;
  flex-direction: column;
  text-align: center;
  height: 30vh;
  background-color: white;
}

.shadow-title {
  padding: 5px;
  font-size: 6rem;
  font-weight: 800;
  color: transparent;
  text-transform: uppercase;
  line-height: 1;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: var(--default-color);
}

.default-hr {
  margin-left: 100px;
  width: 30%;
  border: solid 1px var(--default-color);
}

@media only screen  and (max-width:600px){

  .title-div{

    display: none;
  }

}



.sponserList {
  display: flex;
  flex-wrap: wrap;
}

.sponsorList > img {
  height: 150px;
  width: 250px;
  object-fit: cover;
  margin-left: 10px;
  margin-top: 5px;
}

.sponsorList > img:hover {
  transition: 0.5s ease-in;
  transform: scale(1.1);
}

.default-hr {
  width: 30%;
  border: solid 2px pink;
}

.shadow-title {
  padding: 5px;
  font-size: 6rem;
  font-weight: 800;
  color: transparent;
  text-transform: uppercase;
  line-height: 1;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: var(--default-color);
}

button.apply-button {
  border-radius: 25px;
  width: 200px;
  font-weight: bold;
  border: none;
}

button.apply-button:hover {
  background-color: var(--default-color);
  border: none;
  outline: none;
}

.speaker-list {
  display: flex;
  flex-wrap: wrap;
}

.speaker {
  padding: 5px;
  margin-top: 20px;
  border-radius:12px;
  border:1px solid rgb(251, 145, 145);
  padding:32px;
}

.speaker > img {
  height: 250px;
  width: 250px;
  object-fit: cover;
}

.speaker-description {
  text-align: center;
  font-weight: bold;
  margin-top: 5px;
  font-size: 16px;
}



@media only screen  and (max-width:600px){

  .speaker > img{
    text-align: center;
  }


}
.contact-component-wrapper {
  background-color: rgb(255, 236, 244);
  margin: 0;
  min-height: 50vh;
}

.contact-component-header {
  padding: 10px;
  margin: 0;
  font-size: 5rem;
  color: transparent;
  text-transform: uppercase;
}
.home-contact-icons {
  font-size: 4rem;
}
.home-contact-icons:hover {
  /* font-size: 4rem; */
  scale: 1.2;
}

.home-contact-link {
  color: inherit;
  transition: ease 0.2s;
  text-decoration: none;
}
.home-contact-link:hover {
  text-decoration: none;
  /* color: var(--default-color); */
}

.home-contact-link:hover .home-contact-heading {
  text-decoration: underline;
}

.footer {
  
  height: 30vh;
  background-color: var(--default-color);
  -webkit-filter: contrast(0.9);
          filter: contrast(0.9);
  color: white;
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  padding: 30px;
  background-image: url(/static/media/logo.fc003e46.png);
  background-position: 12px;
  background-repeat: no-repeat;
}

@media only screen and (max-width:768px){
  .footer{
    background-image:none;
  }
}

.footer-main-title {
  font-weight: bold;
  text-shadow: 2px 2px blue;
}

